import * as React from 'react';
import { Node, Controller } from '@ali/4ever-cangjie';

export enum LaserPointerRole {
  /** 主持人 */
  HOST,
  /** 观众 */
  AUDIENCE,
}

export interface LaserPointerPluginConfig {
  /** 是否激光笔开启 */
  isLaserPointerEnabled: () => boolean;
  /** 角色 */
  getRole: () => LaserPointerRole;
  /** 高亮类型 */
  getHighlightType: (node: Node, controller: Controller) => string | null;
  /** 自定义 highlight 渲染方式 */
  renderHighlight: (
    type: string,
    highlighted: boolean,
    props: {
      /** DOM 节点 */
      getDOMNode: () => Element | undefined;
      /** children */
      children: React.ReactNode;
      /** 高亮方法 */
      highlight: () => void;
      /** 解除高亮方法 */
      removeHighlight: () => void;
    },
  ) => React.ReactElement;
}
